<template>
  <div class="formTool-page">
    <div class="formTool-box">
      <div class="zzc" v-if="openPortability"></div>
      <h-portabilityBox
        :openPortability="openPortability"
        :title="componentTitle"
        :top="'100px'"
        :zIndex="21"
        @closePortability="closePortability"
      >
        <component
          :is="componentModul"
          :formName="'新建表格'"
          @componentChange="componentChange"
        ></component>
      </h-portabilityBox>
      <!-- 头部 -->
      <div class="formTool-title">
        <div class="set-tit-box">
          <div
            class="img-box"
            v-for="item in titleConfig"
            :key="item.onlyCode"
            @click="openSetModel(item)"
          >
            <img :title="item.text" :src="item.imgUrl" alt="" />
          </div>
        </div>
      </div>
      <!-- 表格部分 -->
      <div class="formTool-content-layout">
        <div class="formTool-content">
          <div class="form-name-box">
            <p class="form-name" :title="formName">{{ formName }}</p>
            <p
              class="preservation"
              :title="
                isPreservationForm ? '已保存至列表' : '当前操作保存至列表'
              "
              :class="{ storage: isPreservationForm }"
            ></p>
          </div>

          <!-- 右键菜单 -->
          <ul class="custom-context-menu hidden">
            <li
              v-for="item in rightClickFun"
              :key="item.code"
              :class="{ division: item.division }"
              @click="clickLi(item.code)"
            >
              {{ item.text }}
            </li>
          </ul>
          <!-- 表格 -->
          <table class="Formtable" :width="tableW + 'px'">
            <!-- 表头部分
              contenteditable="true"
              @input="changeText($event, tdItem.id)"
            -->
            <thead>
              <tr>
                <th
                  v-for="(thItem, thIndex) in initFormObj.th"
                  :key="thIndex"
                  :width="thItem.thW + 'px'"
                  height="26px"
                >
                  {{ thItem.text }}
                  <p class="drag-ele"></p>
                </th>
              </tr>
            </thead>
            <tbody class="Formtable-body">
              <!-- 表格内容
               @input="changeText($event, tdItem.id)"
              -->
              <tr v-for="(tdArr, index) in initFormObj.td" :key="index">
                <td
                  v-for="(tdItem, tdIndex) in tdArr"
                  :key="tdIndex"
                  :contenteditable="tdItem.isEdit"
                  :height="tdItem.tdH + 'px'"
                  @focus="obtainFocus(tdItem)"
                  @blur="loseBlur($event, tdItem)"
                  :style="{
                    textAlign: tdItem.textPosition
                  }"
                >
                  {{ tdItem.text }}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  onBeforeUnmount
} from 'vue';
import { getLS, setLS, removeLS } from '@/utils/common.js';
import {
  rightClickFun,
  FUN_TYPE,
  titleConfig,
  TITLE_TYPE
} from './formTool.js';
import insertForm from './insert_form.vue';
import formList from './form_list.vue';
export default {
  name: '',
  components: { insertForm, formList },
  setup() {
    const data = reactive({
      formName: getLS('SubTopFormName').length
        ? getLS('SubTopFormName')
        : '默认表格', //当前表格名字
      formId: getLS('SubTopFormId').length
        ? getLS('SubTopFormId')
        : Date.now().toString(16), //当前表格id
      isPreservationForm: true, //标识当前是否改动需要保存
      openPortability: false, //控制打开移动窗口
      componentTitle: '', //窗口标题
      componentModul: '', //窗口显示模块
      initWItem: 100, //默认每个宽度
      targetTD: {
        col: 0,
        row: 0
      },
      titleConfig,
      rightClickFun, //右键菜单配置
      tableSize: [3, 5], //5行  20列
      tableW: 0,
      initFormObj: getLS('SubTopForm'), //获取本地列表数据
      formAllList: getLS('SubTopFormAllList')
    });
    onBeforeMount(() => {});
    onBeforeUnmount(() => {
      window.removeEventListener('keydown', preservationFn);
    });
    onMounted(() => {
      initForm(false); //初始化表格
      monitorRightClick(); //监听右键
      window.addEventListener('keydown', preservationFn);
      addHeightEle();
    });
    // 添加控制高度的元素
    const addHeightEle = () => {
      setTimeout(() => {
        // 下面是为td头部控制高度的元素动态添加子元素，不动态创建会出现问题
        let tdele = Array.from(document.getElementsByTagName('td'));
        tdele.forEach((item, index) => {
          //获取第一个元素
          if (item.height !== 'undefinedpx' && item.childElementCount !== 1) {
            // 创建p标签
            let para = document.createElement('p');
            para.style.cssText =
              'height:5px;position: absolute;width: 100%;left: 0;bottom: -2px; cursor: s-resize;';
            para.classList.add('drag-ele-row');
            item.appendChild(para);
          }
        });
      }, 0);
    };

    // 监听宽度拖拽改变
    const dragChangeWidth = () => {
      setTimeout(() => {
        // 获取顶部th右侧拖拽元素
        let ele = Array.from(document.getElementsByClassName('drag-ele'));
        // 遍历所有元素增加点击和拖拽事件
        ele.forEach((item, index) => {
          item.onmousedown = function (e1) {
            const dataThW = data.initFormObj.th[index].thW;
            // 鼠标位置
            const initLeft = e1.clientX;
            // 表格总宽度
            const initTotalWidth = data.tableW;
            //元素初始宽度，单个th初始宽度
            const eleInitWidth = parseInt(e1.target.parentNode.width);
            // console.log(eleInitWidth);
            // 拖拽事件
            document.onmousemove = function (e2) {
              const moveAfter = e2.clientX; //移动位置
              const addX = moveAfter - initLeft; //增加的绝对距离
              // 缩小时限制,最小为30px宽度
              if (eleInitWidth + addX < 30) return;
              // 设置移动后的宽度  初始宽度加上绝对距离
              e1.target.parentNode.width = eleInitWidth + addX;
              // 改变数据的thw值
              data.initFormObj.th[index].thW = dataThW + addX;
              // 改变整体表格宽度
              data.tableW = initTotalWidth + addX;
            };
            // 取消拖拽事件
            document.onmouseup = function () {
              document.onmouseup = null;
              document.onmousemove = null;
              // 鼠标抬起时存储本地
              setLS('SubTopForm', data.initFormObj);
            };
          };
        });
      }, 0);
    };
    // 监听高度改变时
    const dragChangeHeight = () => {
      setTimeout(() => {
        // 获取顶部th右侧拖拽元素
        let ele = Array.from(document.getElementsByClassName('drag-ele-row'));
        // 遍历所有元素增加点击和拖拽事件
        ele.forEach((item, index) => {
          item.onmousedown = function (e1) {
            const dataTdH = data.initFormObj.td[index][0].tdH;
            // 鼠标位置
            const initTop = e1.clientY;
            //元素初始宽度，单个th初始宽度
            const eleInitHeight = parseInt(e1.target.parentNode.height);

            // 拖拽事件
            document.onmousemove = function (e2) {
              const moveAfter = e2.clientY; //移动位置
              const addX = moveAfter - initTop; //增加的绝对距离
              // 缩小时限制,最小为30px宽度
              if (eleInitHeight + addX < 30) return;
              // 设置移动后的宽度  初始宽度加上绝对距离
              e1.target.parentNode.height = eleInitHeight + addX;
              // 改变数据的thw值
              data.initFormObj.td[index][0].tdH = dataTdH + addX;
            };
            // 取消拖拽事件
            document.onmouseup = function () {
              document.onmouseup = null;
              document.onmousemove = null;
              // 鼠标抬起时存储本地
              setLS('SubTopForm', data.initFormObj);
            };
          };
        });
      }, 0);
    };
    // 监听保存事件
    const preservationFn = (e) => {
      // 监听表格保存ctrl+s按键,未保存时才可再次保存
      e = e || window.event;
      if (
        e.keyCode == 83 &&
        (navigator.platform.match('Mac') ? e.metaKey : e.ctrlKey)
      ) {
        e.preventDefault();
        // 已保存就不执行再次保存
        if (data.isPreservationForm) return;
        data.isPreservationForm = true; //显示已保存
        // 需要保存到列表的表格数据
        const addForm = {
          formId: data.formId,
          formName: data.formName,
          formTable: data.initFormObj
        };
        // 保存本地是不是存在当前表格，根据id
        let cIndex = -1;
        // 每次保存到列表是都要从新获取本地，防止有删除信息
        data.formAllList = getLS('SubTopFormAllList');
        data.formAllList.forEach((item, index) => {
          if (item.formId === data.formId) {
            cIndex = index;
          }
        });
        // 如果存在替换数据
        if (cIndex !== -1) {
          data.formAllList.splice(cIndex, 1, addForm);
        } else {
          // 不存在直接增加
          data.formAllList.unshift(addForm);
        }
        setLS('SubTopFormAllList', data.formAllList);
      }
    };
    // 监听鼠标右键
    const monitorRightClick = () => {
      // 获取监听需要监听右键的元素--表格内容部分
      let tableEle = document.querySelector('.Formtable-body');
      // 监听右键事件
      tableEle.addEventListener('contextmenu', (e) => {
        // 判断右键点击的是不是可编辑元素,是表格中单元格就显示右键菜单
        // 这里是为了清除点击出现右键菜单的问题
        if (e.target.isContentEditable) {
          // 取消默认事件
          e.preventDefault();
          // 获取右键菜单元素
          let menu = document.getElementsByTagName('ul')[0];
          // 设置菜单位置
          menu.style.top = `${e.clientY}px`;
          menu.style.left = `${e.clientX}px`;
          // 移除隐藏,显示元素
          menu.classList.remove('hidden');
        }
      });
      let formToolPage = document.querySelector('.formTool-page');
      // 左键点击清除弹窗
      formToolPage.addEventListener('click', () => {
        hiddenMenu();
      });
    };
    // 隐藏右键菜单
    const hiddenMenu = () => {
      let menu = document.querySelector('.custom-context-menu');
      menu.classList.add('hidden');
    };
    // 初始化表格内容
    const initForm = (bool) => {
      const initHItem = 30; //默认每个宽度  除表头外
      // bool判断不是用默认，再次新建
      if (data.initFormObj.td && !bool) {
        //如果本地有列表数据就不在初始
        calculationTableW(); //计算表格长度
      } else {
        // data.tableW = data.tableSize[1] * data.initWItem; //计算表个宽度
        // 无本地数据就初始化列表
        data.initFormObj = {
          th: null,
          td: []
        };
        let thArr = [];
        // 表头配置
        for (let d = 0; d < data.tableSize[1]; d++) {
          if (d) {
            // 第一行配置 除第一列  textb编号   控制宽度
            thArr.push({
              id: `th/${d}`,
              text: 'col-' + d,
              thW: data.initWItem //默认宽度120px
            });
          } else {
            // 第一行第一列
            thArr.push({
              id: `th/${d}`,
              text: '',
              thW: 30 //默认宽度120px
            });
          }
        }
        data.initFormObj.th = thArr;
        // 表内容梅格配置
        for (let i = 0; i < data.tableSize[0]; i++) {
          let tdArr = []; //保存每行的数组
          for (let d = 0; d < data.tableSize[1]; d++) {
            // 每行除第一例的配置
            if (d) {
              // 除第一列外  无text  文字默认靠左
              tdArr.push({
                id: `td/${i + 1}/${d}`,
                tdCol: d,
                tdRow: i + 1,
                text: '',
                isEdit: true,
                textPosition: 'left'
              });
            } else {
              // 第一行的配置 区别  有text编号  文字居中  控制高度 不可编辑
              tdArr.push({
                id: `td/${i + 1}/${d}`,
                text: i + 1,
                tdH: initHItem,
                isEdit: false,
                textPosition: 'center'
              });
            }
          }
          data.initFormObj.td.push(tdArr);
        }
        // 计算长度
        calculationTableW();
        // console.log(data.initFormObj);
      }
      // 监听拖拽事件
      addHeightEle();
      dragChangeWidth(); //监听表格都不拖拽事件
      dragChangeHeight();
    };
    // 点击右键菜操作,添加行和列,删除行和列
    const clickLi = (code) => {
      const thArr = data.initFormObj.th; //th全部数组
      const tdArr = data.initFormObj.td; //td全部数组
      const targetRow = data.targetTD.row; //目标行位置
      const targetCol = data.targetTD.col; //目标列位置
      // 添加行    拷贝当前元素，增加前面一行
      const addObj = JSON.parse(JSON.stringify(tdArr[targetRow - 1]));
      const addThObj = JSON.parse(JSON.stringify(thArr[targetCol]));
      // 向上增加一行
      if (code === FUN_TYPE.ADDROW_A) {
        // 添加行
        // 除了序号  ，只需要清除内容
        addObj.forEach((item, index) => {
          if (index) {
            item.text = '';
          }
        });
        console.log(addObj);
        // 插入前一行
        tdArr.splice(targetRow - 1, 0, addObj);
        // 处理插入行后面的数据
        addRowAfter(targetRow, tdArr);
      }
      // 向下增加一行
      if (code === FUN_TYPE.ADDROW_B) {
        // 向下插入要清除text , 编号加1，行加1，id加1
        addObj.forEach((item, index) => {
          if (index) {
            item.text = '';
          } else {
            item.text += 1;
          }
          const idArr = item.id.split('/'); //id分割为数组， td  行  列
          //，id加1
          item.id = `${idArr[0]}/${parseInt(idArr[1]) + 1}/${idArr[2]}`;
          // 行加1
          item.tdRow += 1;
        });

        // 插入后一行
        tdArr.splice(targetRow, 0, addObj);
        addRowAfter(targetRow + 1, tdArr);
      }
      // 向前添加一列
      if (code === FUN_TYPE.ADDCOL_A) {
        // 处理头部信息
        thArr.splice(targetCol, 0, addThObj);
        addColAfter(targetCol, thArr, tdArr, false);
      }
      // 向后添加一列
      if (code === FUN_TYPE.ADDCOL_B) {
        // 处理头部信息
        thArr.splice(targetCol + 1, 0, addThObj);
        addColAfter(targetCol, thArr, tdArr, true);
      }
      // 删除当前行
      if (code === FUN_TYPE.DELROW) {
        tdArr.splice(targetRow - 1, 1);
        delRowAfter(targetRow - 1, tdArr);
      }
      // 删除当前列
      if (code === FUN_TYPE.DELCOL) {
        thArr.splice(targetCol, 1);
        delColAfter(targetCol, thArr, tdArr);
      }
      setLS('SubTopForm', data.initFormObj);
      data.isPreservationForm = false; //需要保存操作
    };
    // 删除当前列后处理
    const delColAfter = (targetCol, thArr, tdArr) => {
      // 处理后面元素
      for (let i = targetCol; i < thArr.length; i++) {
        const item = thArr[i];
        // 处理id
        const idArr = item.id.split('/');
        thArr[i].id = `${idArr[0]}/${parseInt(idArr[1]) - 1}`;
        // 处理text
        const textArr = item.text.split('-');
        thArr[i].text = `${textArr[0]}-${parseInt(textArr[1]) - 1}`;
      }
      // 遍历每一行,每一行中增加一个元素
      tdArr.forEach((tdItem) => {
        // 判断向前或向后增加列时,追加的位置
        tdItem.splice(targetCol, 1);
        // 处理后面元素
        for (let i = targetCol; i < tdItem.length; i++) {
          const item = tdItem[i];
          // 处理id   列加1
          const idArr = item.id.split('/');
          tdItem[i].id = `${idArr[0]}/${idArr[1]}/${parseInt(idArr[2]) - 1}`;
          // 处理tdCol + 1
          tdItem[i].tdCol -= 1;
        }
      });
      console.log(tdArr);
      // 更新列表宽度
      //计算表个宽度
      calculationTableW();
    };
    // 处理增加列后面的需要和内容调整
    const addColAfter = (targetCol, thArr, tdArr, bool) => {
      // 处理后面元素
      for (let i = targetCol + 1; i < thArr.length; i++) {
        const item = thArr[i];
        // 处理id
        const idArr = item.id.split('/');
        thArr[i].id = `${idArr[0]}/${parseInt(idArr[1]) + 1}`;
        // 处理text
        const textArr = item.text.split('-');
        thArr[i].text = `${textArr[0]}-${parseInt(textArr[1]) + 1}`;
      }
      // 遍历每一行,每一行中增加一个元素
      tdArr.forEach((tdItem) => {
        // 拷贝增加的元素
        const addTdObj = JSON.parse(JSON.stringify(tdItem[targetCol]));
        // 清除text,改变后面的id和col值
        addTdObj.text = '';
        // 判断向前或向后增加列时,追加的位置
        const inTargetCol = bool ? targetCol + 1 : targetCol;
        tdItem.splice(inTargetCol, 0, addTdObj);
        // 处理后面元素
        for (let i = targetCol + 1; i < tdItem.length; i++) {
          const item = tdItem[i];
          // 处理id   列加1
          const idArr = item.id.split('/');
          tdItem[i].id = `${idArr[0]}/${idArr[1]}/${parseInt(idArr[2]) + 1}`;
          // 处理tdCol + 1
          tdItem[i].tdCol += 1;
        }
      });
      // 增加拖拽事件
      dragChangeWidth();
      // 计算表个宽度
      calculationTableW();
    };
    // 删除当前行后处理
    const delRowAfter = (targetRow, tdArr) => {
      for (let i = targetRow; i < tdArr.length; i++) {
        tdArr[i].forEach((item, index) => {
          // 第一行改变序号加1
          if (index === 0) {
            item.text -= 1;
          }
          const idArr = item.id.split('/'); //id分割为数组， td  行  列
          // 行加1
          item.id = `${idArr[0]}/${parseInt(idArr[1]) - 1}/${idArr[2]}`;
          item.tdRow -= 1;
        });
      }
    };
    // 处理增加行后面的需要和内容调整
    const addRowAfter = (targetRow, tdArr) => {
      for (let i = targetRow; i < tdArr.length; i++) {
        tdArr[i].forEach((item, index) => {
          // 第一行改变序号加1
          if (index === 0) {
            item.text += 1;
          }
          const idArr = item.id.split('/'); //id分割为数组， td  行  列
          // 行加1
          item.id = `${idArr[0]}/${parseInt(idArr[1]) + 1}/${idArr[2]}`;
          item.tdRow += 1;
        });
      }
      // 监听拖拽事件
      addHeightEle();
      dragChangeHeight();
    };
    // 每次输入触发
    // const changeText = (e, id) => {
    //   console.log(e.target.innerText, id);
    // };
    // 获取焦点时,获取目标元素位置
    const obtainFocus = (tdItem) => {
      data.targetTD.col = tdItem.tdCol;
      data.targetTD.row = tdItem.tdRow;
    };
    // 失去焦点触发
    const loseBlur = (e, tdItem) => {
      if (
        data.initFormObj.td[tdItem.tdRow - 1][tdItem.tdCol].text ==
        e.target.innerText
      )
        return; //内容相等就不保存
      // 通过行和列找到对应对象改变内容
      data.initFormObj.td[tdItem.tdRow - 1][tdItem.tdCol].text =
        e.target.innerText;
      setLS('SubTopForm', data.initFormObj);
      data.isPreservationForm = false; //控制是否保存
    };

    // 打开工具窗口
    const openSetModel = (val) => {
      data.openPortability = true;
      data.componentModul = val.onlyCode;
      data.componentTitle = val.text;
    };
    const closePortability = () => {
      data.openPortability = false;
    };
    // 模块触发事件
    const componentChange = (val, type) => {
      data.openPortability = false;
      // 新建表格
      if (type === TITLE_TYPE.INSERT_FORM) {
        // 新建表格
        data.tableSize = [val.setRow, val.setCol];
        data.isPreservationForm = false; //控制是否保存
        data.formName = val.myFormName;
        // 新建表格后，设置新的id
        data.formId = Date.now().toString(16);
        setLS('SubTopFormName', val.myFormName);
        setLS('SubTopFormId', data.formId);
        initForm(true); //新建
      }
      // 点击显示表单的
      if (type === TITLE_TYPE.FORM_LIST) {
        data.initFormObj = val.formTable;
        data.formName = val.formName;
        data.formId = val.formId;
        data.isPreservationForm = true; //控制是否保存
        // 选择是同步更新本地当前编辑的数据信息
        setLS('SubTopFormName', val.formName);
        setLS('SubTopFormId', data.formId);
        setLS('SubTopForm', val.formTable);
        initForm(false); //展示历史
      }
    };

    // 更新表格宽度,将当前头部宽度相加
    const calculationTableW = () => {
      let num = 0;
      data.initFormObj.th.forEach((item) => {
        num += item.thW;
      });
      data.tableW = num;
    };

    return {
      componentChange,
      openSetModel,
      obtainFocus,
      clickLi,
      loseBlur,
      closePortability,
      // changeText,
      ...toRefs(data)
    };
  }
};
</script>
<style scoped lang="less">
.custom-context-menu {
  position: fixed;
  width: 200px;
  height: 200px;
  border: 1px solid #dadce0;
  box-shadow: 4px 4px 2px -2px #808080;
  background: #fff;
