<template>
  <div class="form-list-model">
    <ul>
      <li
        v-for="(item, index) in allList"
        :key="index"
        @click="openListForm(item)"
      >
        <p class="form-name">{{ item.formName }}</p>
        <p
          class="iconfont icon-shanchu deleted"
          @click.stop="delForm(index)"
        ></p>
      </li>
    </ul>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
import { getLS ,setLS} from '@/utils/common.js';
import { TITLE_TYPE } from './formTool.js';
export default {
  name: '',
  setup(props, ctx) {
    const data = reactive({
      allList: getLS('SubTopFormAllList')
    });
    onBeforeMount(() => {});
    onMounted(() => {});
    const openListForm = (val) => {
      ctx.emit('componentChange', val, TITLE_TYPE.FORM_LIST);
    };
    const delForm = (index) => {
      data.allList.splice(index, 1);
      setLS('SubTopFormAllList', data.allList);
    };
    return {
      delForm,
      openListForm,
      ...toRefs(data)
    };
  }
};
</script>
<style scoped lang="less">
.form-list-model {
  width: 400px;
  height: 700px;
  overflow: auto;
  &::-webkit-scrollbar {
    width: 7px;
  }
  /* 滚动槽 */
  &::-webkit-scrollbar-track {
    border-radius: 10px;
    background: rgb(224, 224, 224);
  }
  /* 滚动条滑块 */
  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: rgb(189, 189, 189);
  }
  ul {
    li {
      line-height: 24px;
      font-size: 14px;
      padding: 0 20px;
      overflow: hidden;
      transition: 0.2s;
      &:hover {
        cursor: pointer;
        background: rgb(245, 245, 245);
        .deleted {
          opacity: 1;
        }
      }
      .form-name {
        float: left;
      }

      .deleted {
        float: right;
        opacity: 0;
        transition: 0.2s;
        &:hover {
          color: red;
        }
      }
    }
  }
}
</style>
