<template>
  <div class="insert-form-model">
    <div class="config-item-box">
      <p class="config-name">表格名称：</p>
      <input class="config-input" type="text" v-model="myFormName" />
    </div>
    <div class="config-item-box">
      <p class="config-name">表列数：</p>
      <input
        class="config-col config-input"
        oninput="if(!/^[0-9]+$/.test(value)) value=value.replace(/\D/g,'');if(value>100)value=100;if(value<1)value=null"
        type="number"
        v-model="setCol"
      />
      <p class="config-name">表行数：</p>
      <input
        class="config-row config-input"
        oninput="if(!/^[0-9]+$/.test(value)) value=value.replace(/\D/g,'');if(value>100)value=100;if(value<1)value=null"
        type="number"
        v-model="setRow"
      />
    </div>
    <p class="new_add" @click="newForm">新建</p>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
import { TITLE_TYPE } from './formTool.js';
export default {
  props: {
    formName: {
      type: String
    }
  },
  name: '',
  setup(props, ctx) {
    console.log('1-开始创建组件-setup');
    const data = reactive({
      myFormName: props.formName,
      setCol: 7,
      setRow: 7
    });
    onBeforeMount(() => {});
    onMounted(() => {});
    // 新建表格 名称不能为空，且不能和本地重复，行列不能为空
    const newForm = () => {
      if (data.setCol && data.setRow) {
        ctx.emit('componentChange', data, TITLE_TYPE.INSERT_FORM);
      }
    };
    return {
      newForm,
      ...toRefs(data)
    };
  }
};
</script>
